import { rudderanalytics } from "./rudderInitalize";

let currentUser; 

function getCurrentUser() {
  return currentUser;
}

function setCurrentUser(user) {
  currentUser = user;
}

export function eventIdentify(user) {
  let cur_User = getCurrentUser();
  if (cur_User === user) {
    // Do nothing
  } else {
    rudderanalytics.reset(true);
    cur_User = user;
    setCurrentUser(cur_User);
  }

  console.log(`Calling RudderStack with userId: ${cur_User}`);
  rudderanalytics.identify(cur_User, 
    {
      name: cur_User,
      category: "Identify User",
      email: `${cur_User}@gmail.com`,
      age: Math.floor(Math.random() * 100),
      logins: Math.floor(Math.random() * 100)
    });
}

export function eventPage(name, payload) {
  rudderanalytics.page(name, payload);
}

